.privacy-policy .title-container .title, .privacy-policy .title-container .desc, .privacy-policy .content .title, .privacy-policy .content .title-desc, .privacy-policy .content .title-letter {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.privacy-policy {
  position: relative;
  overflow: hidden; }
  .privacy-policy .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 10px; }
    .privacy-policy .title-container .title {
      display: inline;
      font-size: 20px;
      font-weight: 700;
      color: #354F53 !important; }
      @media screen and (min-width: 1440px) {
        .privacy-policy .title-container .title {
          font-size: 34px; } }
    .privacy-policy .title-container .desc {
      text-align: center;
      font-size: 16px;
      font-weight: normal;
      color: #354F53 !important; }
      @media screen and (min-width: 1440px) {
        .privacy-policy .title-container .desc {
          font-size: 19.5; } }
  .privacy-policy .content .content-row {
    justify-content: center;
    margin-bottom: 50px;
    padding: 20px; }
    @media screen and (min-width: 768px) {
      .privacy-policy .content .content-row {
        padding: 40px; } }
  .privacy-policy .content .title {
    font-size: 16px;
    font-weight: bold;
    color: #354F53;
    text-align: left !important;
    width: 80vw;
    margin: auto; }
    @media screen and (min-width: 1440px) {
      .privacy-policy .content .title {
        font-size: 16px; } }
  .privacy-policy .content .title-desc {
    font-size: 14px;
    color: #46737A;
    text-align: left !important;
    padding: 10px 0;
    width: 80vw;
    margin: auto; }
    @media screen and (min-width: 1440px) {
      .privacy-policy .content .title-desc {
        font-size: 16px; } }
    .privacy-policy .content .title-desc b {
      font-weight: bold; }
  .privacy-policy .content ul {
    list-style: none;
    padding: 0; }
    .privacy-policy .content ul li {
      padding: 10px 0; }
  .privacy-policy .content .title-letter {
    font-size: 14px;
    color: #46737A;
    text-align: left !important;
    padding: 10px;
    width: 80vw;
    margin: auto; }
    @media screen and (min-width: 1440px) {
      .privacy-policy .content .title-letter {
        font-size: 16px; } }
    .privacy-policy .content .title-letter b {
      font-weight: bold; }
  .privacy-policy .content ul {
    list-style: none;
    padding: 0; }
    .privacy-policy .content ul li {
      padding: 10px 0; }
